import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { ExpenseReportsComponent } from './expense-reports.component';
import { ExpenseReportsVendorComponent } from './expense-reports-vendor/expense-reports-vendor.component';

export const routes: Routes = [
  { path: '', component: ExpenseReportsComponent },
  { path: 'vendor', component: ExpenseReportsVendorComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpenseReportsRoutingModule { }
