import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';

@Component({
  selector: 'app-expense-reports-vendor',
  templateUrl: './expense-reports-vendor.component.html',
  styleUrls: ['./expense-reports-vendor.component.scss']
})
export class ExpenseReportsVendorComponent implements OnInit {

  blockData:any;
  vendorData:any;

  report:any;
  
  glAccountsDataList:any;
  glDocumentTypeDataList:any;

  glAccountIndicator:any = "Liability"

  isBlockSelected:boolean = false;
  isUnitSelected:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 8;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isReportSubmitted:boolean = true;
  isDataLoaded:boolean = true;

  reportsDataList:any;
  reportsData:any;

  constructor(
  	private apartmentService:ApartmentService,
  	private vendorService:VendorService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getDate(date){
  	if(date != null)
    return moment(date).format("MM-DD-YYYY");
	else
	return ""
  }

  getSelectedGlAccount(){
    this.report.GLaccountID = event[0].glaccountId
  }

  getSelectedVendor(event){
    this.report.vendorId = event[0].vendorId
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  getDateFormat(date){
    return moment(date).format("YYYY-MM-DD");
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  submitExpenseReportsVendorForm(form) {

  	this.isReportSubmitted = true;
    this.isDataLoaded = false;
   
  	let details = {
  	  BlockID: null,
  	  DocumentdateFrom: this.getDateFormat(this.report.documentDateFrom),
  	  DocumentdateTo: this.getDateFormat(this.report.documentDateTo),
      PostingdateFrom: this.getDateFormat(this.report.PostingdateFrom),
      PostingdateTo: this.getDateFormat(this.report.PostingdateTo),
      VendorId: parseInt(this.report.vendorId), //3,
      GLaccountID: parseInt(this.report.GLaccountID),
      GLDocumentTypeID: 149, //parseInt(this.report.GLDocumentTypeID),
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }


  	this.accountsService.getVendorGltransactionsMultiFilter(details).subscribe((res:any) => {

    	this.reportsDataList = res;
    	
    	console.log(this.reportsDataList);

    	this.totalItems = this.reportsDataList.length;

		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

		this.isDataLoaded = true;
		this.isReportSubmitted = false;

    },
    error => {
      
    });

  }

  ngOnInit() {

  	this.report = {};
  	this.report.apartmentBlockId = "";
  	this.report.vendorId = "";

  	this.report.GLaccountID = "";
    this.report.GLDocumentTypeID = "";
    
    let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res:any) => {
      this.blockData = res;
    });

    let vendorParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.vendorService.getVendorByApartmentId(vendorParams).subscribe((res:any) => {
    	this.vendorData  =res;
    });

  	 this.accountsService.getAllGlAccounts().subscribe((res:any) => {
      
      this.glAccountsDataList = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator ==  this.glAccountIndicator;
      });


  	});

    //Gl Document 

    let documentParams = {
      LookupTypeId: 31
    }

    this.lookupService.getLookupValueByLookupTypeId(documentParams).subscribe((res:any) => {
      this.glDocumentTypeDataList = res;
    },
    error => {
      
    });

  }

}
